@import url('https://fonts.googleapis.com/css2?family=Overpass:wght@400;700&display=swap');

:root {
  --Orange: hsl(25, 97%, 53%);
  --White: hsl(0, 0%, 100%);
  --Light-Grey: hsl(217, 12%, 63%);
  --Medium-Grey: hsl(216, 12%, 54%);
  --Dark-Blue: hsl(213, 19%, 18%);
  --Very-Dark-Blue: hsl(216, 12%, 8%);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Overpass;
  color: var(--White);
  background: var(--Very-Dark-Blue);
}

#test{
  
  filter: invert(57%) sepia(87%) saturate(2961%) hue-rotate(351deg) brightness(98%) contrast(101%);
}

#star {
  display: block;
  width: 40px;
  height: 40px;
  background: url("./icon-star.svg") no-repeat center var(--Dark-Blue);
  background-size: 15px;
  border-radius: 50%;
  margin-bottom: 15px;
}

.App, #done  {
  position: absolute;
  font-size: 16px;
  top: 20%;
  left: 50%;
  transform: translate(-50%);
  width: 90vw;
  height: 50vh;
  padding: 20px;
  background: hsl(210, 18%, 13%);
  border-radius: 15px;
}

h1{
  color: var(--White);
}

p {
  margin-top: 5px;
  font-size: 14px;
  margin-bottom: 10px;
  color: var(--Medium-Grey);
}

span, span > p {
  border-radius: 50%;
  display: inline-flex;
  width: 45px;
  height: 45px;
  margin: 5px;
  justify-content: center;
  align-items: center;
  color: var(--Light-Grey);
  background-color: var(--Dark-Blue);
  cursor: pointer;
}

 span:hover{
  background-color: var(--Orange);
  color: var(--White);
} 

.ratings {
  display: flex;
  justify-content: space-evenly;
  margin-top: 15px;
  margin-bottom: 15px;
}

button {
  background: var(--Orange);
  border: none;
  height: 50px;
  padding: 10px;
  color: var(--White);
  font-weight: 700;
  border-radius: 30px;
  width: 100%;
}

button:hover{
  color: var(--Orange);
  background-color: var(--White);
}

.attribution{
  position: absolute;
  font-size: 14px;
  top: 100%;
  text-align: center;
}

.attribution a{
  text-decoration: none;
  color: var(--Orange);
}
/* Thank you card */

#done {
  display: none;
  text-align: center;
}

#thank-you{
  background: url('./thankyouemoji.gif') no-repeat center;
  background-size: 150px;
  margin-top: 10px;
  margin-bottom: 20px;
  height: 100px;
  width: 100%;
}

#done > h1{
  margin-top: 20px;
  margin-bottom: 15px;
}

#rate{
  display: flex;
  justify-content: center;
  width: 220px;
  height: 30px;
  margin: 0 auto;
  border-radius: 30px;
  background: var(--Dark-Blue);
  margin-bottom: 10px;
}

#rate > p{
  font-size: 15px;
  color: var(--Orange);
}

/* Above mobile view */

@media(min-width:380px){
  .App, #done{
    width: 350px;
  }
}